import { useMemo } from "react"
import { CqsItem } from "@rp/common/api-types"
import { Watchlist } from "@rp/common/api-types"
import { ShowMoreButton } from "@rp/react/web/common/composite-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { FilterInput, FilterInputMobile } from "../FilterInput"

const MAX_INITIAL_FILTERS = 5
const MAX_TOTAL_FILTERS = 15

type FilterDisplayProps = {
  filters: Array<CqsItem>
  disableCheckbox?: boolean
  disableWatchlist?: boolean
  onChange?: (item: CqsItem) => void
  onClick?: (item: CqsItem) => void
  onExclude?: (item: CqsItem) => void
  showMore?: boolean
  toggleShowMore: () => void
  onAddToWatchlist?: (watchlist: Watchlist) => void
}

const FilterDisplay = ({
  filters,
  disableCheckbox,
  disableWatchlist,
  onChange,
  onClick,
  showMore = false,
  toggleShowMore,
  onAddToWatchlist,
  onExclude,
}: FilterDisplayProps) => {
  const { isMobile } = useDeviceInfo()
  const showMoreItems = filters.slice(MAX_INITIAL_FILTERS, MAX_TOTAL_FILTERS)

  const FilterInputComponent = useMemo(
    () => (isMobile ? FilterInputMobile : FilterInput),
    [isMobile],
  )

  return (
    <>
      {filters.slice(0, MAX_INITIAL_FILTERS).map(filter => (
        <FilterInputComponent
          disableCheckbox={disableCheckbox}
          key={`${filter.key}-selector`}
          filterItem={filter}
          onChange={onChange}
          onClick={onClick}
          onAddToWatchlist={onAddToWatchlist}
          onExclude={onExclude}
          disableWatchlist={disableWatchlist}
        />
      ))}

      {showMore
        ? showMoreItems.map(filter => (
            <FilterInputComponent
              disableCheckbox={disableCheckbox}
              key={`${filter.key}-selector`}
              filterItem={filter}
              onChange={onChange}
              onClick={onClick}
              onAddToWatchlist={onAddToWatchlist}
              onExclude={onExclude}
              disableWatchlist={disableWatchlist}
            />
          ))
        : null}
      {filters.length > MAX_INITIAL_FILTERS ? (
        <ShowMoreButton showMore={showMore} onClick={toggleShowMore} qty={showMoreItems.length} />
      ) : null}
    </>
  )
}

export { FilterDisplay }
